<template>
  <div class="article-detail">
    <h2>4.1 Unicode</h2>
    <p>JavaScript \uxxxxxxxx Unicode </p>
    <pre>
      <code>
        "\u0061"
        // "a"
      </code>
    </pre>
    <p>\u0000~\uFFFF</p>
    <pre>
      <code>
        "\uD842\uDFB7"
        // ""

        "\u20BB7"
        // " 7"
      </code>
    </pre>
    <p>\u0xFFFF\u20BB7JavaScript\u20BB+7\u20BB7</p>
    <p>ES6 </p>
    <pre>
      <code>
        "\u{20BB7}"
        // ""

        "\u{41}\u{42}\u{43}"
        // "ABC"

        let hello = 123;
        hell\u{6F} // 123

        '\u{1F680}' === '\uD83D\uDE80'
        // true
      </code>
    </pre>
    <p> UTF-16 </p>
    <p>JavaScript 6</p>
    <pre>
      <code>
        '\z' === 'z'  // true
        '\172' === 'z' // true
        '\x7A' === 'z' // true
        '\u007A' === 'z' // true
        '\u{7A}' === 'z' // true
      </code>
    </pre>
    
    <hr>

    <h2>4.2 codePointAt()</h2>
    <p>JavaScriptUTF-1624Unicode0xFFFFJavaScript</p>
    <pre>
      <code>
        var s = "";

        s.length // 2
        s.charAt(0) // ''
        s.charAt(1) // ''
        s.charCodeAt(0) // 55362
        s.charCodeAt(1) // 57271
      </code>
    </pre>
    <p>0x20BB7UTF-160xD842 0xDFB755362 5727144JavaScript2charAtcharCodeAt</p>
    <p>ES6codePointAt4</p>
    <pre>
      <code>
        var s = 'a';

        s.codePointAt(0) // 134071
        s.codePointAt(1) // 57271

        s.codePointAt(2) // 97
      </code>
    </pre>
    <p>codePointAt0JavaScriptacodePointAt13407120BB7acodePointAtcharCodeAt</p>
    <p>codePointAt32UTF-16charCodeAt</p>
    <p>codePointAttoString</p>
    <pre>
      <code>
        var s = 'a';

        s.codePointAt(0).toString(16) // "20bb7"
        s.codePointAt(2).toString(16) // "61"
      </code>
    </pre>
    <p>codePointAtas1codePointAt2for...of32UTF-16</p>
    <pre>
      <code>
        var s = 'a';
        for (let ch of s) {
          console.log(ch.codePointAt(0).toString(16));
        }
        // 20bb7
        // 61
      </code>
    </pre>
    <p>codePointAt</p>
    <pre>
      <code>
        function is32Bit(c) {
          return c.codePointAt(0) > 0xFFFF;
        }

        is32Bit("") // true
        is32Bit("a") // false
      </code>
    </pre>
  </div>
</template>

<script>
export default {
  name: 'c1',
  data () {
    return {
    }
  },
  components: {
  },
  methods: {
  },
  created () {
  }
}
</script>



