<template>
  <div class="article-detail">
    <h2>8.1 </h2>
    <h4></h4>
    <p>
      ES6
    </p>
    <pre>
      <code>
        function log(x, y) {
          y = y || 'World'
          console.log(x, y)
        }

        log('Hello') // Hello World
        log('Hello', 'China')  Hello China
        log('Hello', '') // Hello World
      </code>
    </pre>
    <p>
      logyWorldyfalsey
    </p>
    <p>
      y
    </p>
    <pre>
      <code>
        // 
        if (typeof y === 'undefined') y = 'World'

        // 
        if (arguments.length === 1) y = 'World'
      </code>
    </pre>
    <p>
      ES6
    </p>
    <pre>
      <code>
        function log(x, y = 'World') console.log(x, y)

        log('Hello') // Hello World
        log('Hello', 'China')  Hello China
        log('Hello', '') // Hello
      </code>
    </pre>
    <p>
      ES6ES5
    </p>
    
    <hr>

    <h4>8.2 rest</h4>
    <p>
      ES6rest...argumentsrest
    </p>
    <pre>
      <code>
        function add (...values) {
          let sum = 0

          for (let val of values) {
            sum += val
          }

          return sum
        }

        add(2, 5, 3) // 10
      </code>
    </pre>

    <hr>

    <h2>5.3 u</h2>
    <p>ES6uUnicode\uFFFFUnicodeUTF-16</p>
    <pre>
      <code>
        /^\uD83D/u.test('\uD83D\uDC2A')
        // false
        /^\uD83D/.test('\uD83D\uDC2A')
        // true
      </code>
    </pre>
    <p>\uD83D\uDC2AUTF-16ES5UTF-16trueuES6false</p>
    <p>u</p>
  </div>
</template>

<script>
export default {
  name: 'c6',
  data () {
    return {
    }
  },
  components: {
  },
  methods: {
    recursion (num) {
      if (num <= 1) {
        return 1
      } else {
        return num + this.recursion(num - 1)
      }
    }
  },
  created () {
    let arr1 = [1]
    let arr2 = [2]
    let arr3 = [3]
    let arr4 = [...arr1, ...arr2, ...arr3]
    console.log(arr4)

    let nodelist = document.querySelectorAll('p')
    console.log([...nodelist])

    let map1 = new Map([[1, 'one'], [2, 'two'], [3, 'three']])
    console.log([...map1.keys()])
    console.log(this.recursion(3))
  }
}
</script>



