<template>
  <div class="article-detail">
    <h2>6.1 </h2>
    <p>ES6</p>
    <pre>
      <code>
        var regex = new RegExp("xyz", "i");
        //
        var regex = /xyz/i;
      </code>
    </pre>
    <p>ES6RegExp</p>
    <pre>
      <code>
        var regex = new RegExp(/xyz/i);
      </code>
    </pre>
    <p>RegExp2</p>
    <pre>
      <code>
        new RegExp(/ab/ig, 'i').flags
        // "i"
      </code>
    </pre>

    <hr>

    <h2>5.2 </h2>
    <p>4match()replace()search()split()</p>
    <p>ES64RegExpRegExp</p>
    <ul>
      <li>String.prototype.match  RegExp.prototype[Symbol.match]</li>
      <li>String.prototype.replace  RegExp.prototype[Symbol.replace]</li>
      <li>String.prototype.search  RegExp.prototype[Symbol.search]</li>
      <li>String.prototype.split  RegExp.prototype[Symbol.split]</li>
    </ul>

    <hr>

    <h2>5.3 u</h2>
    <p>ES6uUnicode\uFFFFUnicodeUTF-16</p>
    <pre>
      <code>
        /^\uD83D/u.test('\uD83D\uDC2A')
        // false
        /^\uD83D/.test('\uD83D\uDC2A')
        // true
      </code>
    </pre>
    <p>\uD83D\uDC2AUTF-16ES5UTF-16trueuES6false</p>
    <p>u</p>
  </div>
</template>

<script>
export default {
  name: 'c6',
  data () {
    return {
    }
  },
  components: {
  },
  methods: {
  },
  created () {
  }
}
</script>



